import React, { Component } from 'react';
import './App.css';

import CardContainer from './layout/CardContainer';
import WorkoutContainer from './layout/WorkoutContainer';
import './styles/shadows.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      addedExercises: []
    }
  }

  clicked = (passedProps) => {
    let { addedExercises } = this.state;
    addedExercises.push(passedProps);
    this.setState({
      addedExercises: addedExercises
    });
  }

  render() {
    return (
      <div className="App">
        <div className="navbar shadow-2">
          <h1>Calisthenic Workout Builder</h1>
        </div>
        <h2>Directions</h2>
        <ol>
          <li>Click to Add Exercises</li>
          <li>Set Number of Reps</li>
          <li>Set Number of Sets</li>
        </ol>
        <div className="main-flex">
          <div className="main-container shadow-2">
            <h2>Chest</h2>
            <CardContainer 
                exercises={chestExercises}
                clicked={this.clicked} />
            <h2>Legs</h2>
            <CardContainer 
                exercises={legsExercises}
                clicked={this.clicked} />
            <h2>Back</h2>
            <CardContainer 
                exercises={backExercises}
                clicked={this.clicked} />
            <h2>Cardio</h2>
            <CardContainer 
                exercises={cardioExercises}
                clicked={this.clicked} />
            <h2>Core</h2>
            <CardContainer 
                exercises={coreExercises}
                clicked={this.clicked} />
          </div>
          <div className="main-container shadow-2">
            <h2>Workout:</h2>
            <WorkoutContainer
                exercises={this.state.addedExercises} />
          </div>
        </div>
      </div>
    );
  }
}

export default App;


